.tag-input {
  font-family: "Raleway", sans-serif;
  width: 125px;
  height: 20px;
  margin: 5px;
  padding: 5px;
  font-size: 18px;
  border: none;
  border-bottom: 2px solid rgba(0, 0, 0, 0.25);
}

.tag-input:focus {
  outline: none;
  border-bottom: 2px solid rgba(0, 0, 0, 0.55);
}
